<template>
  <div class="news-view">
    <div class="news-view-content">
      <div class="news-view-content-left">
        <el-breadcrumb class="news-breadcrumb" separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: secondLevel.url }">{{ secondLevel.title }}</el-breadcrumb-item>
          <el-breadcrumb-item class="news-breadcrumb-curr-title">{{ newsData ? newsData.title : '' }}
          </el-breadcrumb-item>
        </el-breadcrumb>
        <div class="news-view-mask" v-loading="loading" element-loading-background="transparent">
          <div class="news-view-wrapper" v-if="newsData">
            <div class="news-view-title">{{ newsData.title }}</div>
            <span class="news-view-date"><i class="iconfont icon-shijian"></i> {{ newsData.mtime }}</span>
            <div class="news-view-divider"></div>
            <div class="news-view-detail" v-html="newsData.content">
              <!--<img src="http://news.xtu.edu.cn/document/photo/pwqrr60xek.jpg" alt="">-->
              <!--<p>318CUBA</p>-->
              <!--<p>2012</p>-->
              <!--<p>8</p>-->
              <!--<p></p>-->
              <!--<p>4651017</p>-->
              <!--<p class="text-right">/  /</p>-->
              <!--<p class="text-right"></p>-->
            </div>
          </div>
        </div>
      </div>
      <div class="news-view-content-right" v-pin>
        <HotSpots/>
      </div>
    </div>
  </div>
</template>

<script>
import HotSpots from '@/base/hot-spots/hot-spots'
import {getNewsByNewsId, handleError} from '@/api'

export default {
  props: {
    newsId: {
      type: Number,
      default: null
    },
    secondLevel: {
      type: Object,
      default: function() {
        return {
          url: '/news',
          title: ''
        }
      }
    }
  },
  data() {
    return {
      loading: false,
      newsData: null
    }
  },
  mounted() {
    if (this.newsId !== null) {
      this._getNewsByNewsId(this.newsId)
    }
  },
  methods: {
    async _getNewsByNewsId(id) {
      this.loading = true
      let data = (await getNewsByNewsId(id)).data
      if (data.code === 0) {
        this.newsData = data.data
      } else {
        handleError(data)
      }
      this.loading = false
    }
  },
  components: {
    HotSpots
  }
}
</script>

<style lang="scss">
.news-view {
  @include limit-width();
  min-height: 610px;

  .news-view-content {
    display: flex;
    .news-view-content-left {
      width: 650px;
      flex-shrink: 0;

      .news-breadcrumb {
        margin-top: 20px;
        line-height: 50px;

        .news-breadcrumb-curr-title {
          @include no-wrap(1);
          width: 30em;
        }
      }

      .news-view-mask {
        min-height: 200px;
        .news-view-wrapper {
          margin-bottom: 20px;

          .news-view-title {
            font-size: 26px;
            line-height: 1.8em;
            color: $color-red;
            font-weight: bold;
          }

          .news-view-date {
            line-height: 3em;
            color: #333;
            font-size: 14px;
            .iconfont {
              font-size: 18px;
              color: $color-yellow;
              font-weight: bold;
            }
          }

          .news-view-divider {
            height: 2px;
            width: 100%;
            background-color: #cfcfcf;
            margin: 5px 0;
          }

          .news-view-detail {
            padding: 15px;

            img {
              max-width: 100% !important;
              height: auto !important;
            }
            /*embed {*/
              /*width: 100%;*/
            /*}*/
            p {
              /*text-indent: 2em;*/
              /*line-height: 1.5em;*/

              &.text-right {
                text-align: right;
              }
            }
          }
        }
      }
    }

    .news-view-content-right {
      padding-top: 35px;
    }
    .pin-wrapper,
    .news-view-content-right {
      flex: 1;
    }
  }
}
</style>



