<template>
  <div class="contribute-content">
    <el-card class="contribute-card">
      <div slot="header">
        <span class="contribute-title"></span>
      </div>
      <el-form :model="contributeFrom" :rules="contributeRules" label-position="right" label-width="100px"
               ref="contributeFrom">
        <el-form-item label="" prop="title">
          <el-input v-model="contributeFrom.title" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="author">
          <el-input v-model="contributeFrom.author" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="tel">
          <el-input v-model="contributeFrom.tel" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="contributeFrom.organization" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="upload">
          <el-upload
            action="/contribute"
            :auto-upload="false"
            :limit="1"
            :on-change="handleFileChange">
            <el-button size="medium" type="primary"></el-button>
          </el-upload>
          <div class="upload-tip"> Word  JPG  ZIP  RAR </div>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="contributeFrom.remark" type="textarea" :rows="3" placeholder=""></el-input>
        </el-form-item>
        <!--  -->

        <el-form-item>
          <el-button type="primary" @click="submitForm"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { PostContribute } from '@/api'

export default {
  data() {
    return {
      contributeFrom: {
        title: '',
        author: '',
        organization: '',
        tel: '',
        remark: ''
      },
      contributeRules: {
        title: [
          {required: true, message: '', trigger: ['blur', 'change']}
        ],
        author: [
          {required: true, message: '', trigger: ['blur', 'change']}
        ],
        tel: [
          {required: true, message: '', trigger: ['blur', 'change']}
        ]
      }
    }
  },
  methods: {
    submitForm() {
      this.$refs.contributeFrom.validate((valid) => {
        if (valid) {
          alert('submit!')
          console.log(this.files)
          const form = this.contributeFrom
          PostContribute(form.title, form.author, form.tel, form.organization, form.remark, this.file)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    handleFileChange(file, fileList) {
      this.file = fileList.length ? fileList[0] : null
      console.log(file, fileList)
    },
    resetForm() {
      this.$refs.contributeFrom.resetFields()
    }
  }
}
</script>

<style lang="scss" scoped>
.contribute-content {
  @include limit-width();
  padding: 35px 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;

  .contribute-card {
    max-width: 650px;
    width: 100%;

    .contribute-title {
      font-weight: bold;
      font-size: 22px;
    }

    .upload-tip {
      font-size: 14px;
      color: #606266;
      line-height: 2em;
      padding-top: 1em;
    }
  }
}
</style>



